.infoCrianca{
    display: block;
    min-width: 200px;
    border-radius: 4px;
    border: 1px solid #30333C; 
    margin-bottom: 10px;
    width: 100%;
  
    &__divTitle{
      height: 30px;
      align-items: center;
      display: flex;
      background: linear-gradient(to right,#3f51b5, #1f61db);
      width: 100%;
    }
  
    &__title{
      margin: 0;
      padding-left: 10px;
      font-weight: 700;
      color: #f8f9fa;
    }
  
    &__divFlex{
      width: 100%;
      display: flex;
      flex-wrap: wrap;
    }
  
    &__img{
      min-width: 200px;
      width: 200px;
      height: 100%;
      border-radius: 5px;
      border: 1px solid #30333C;
      margin: 10px 0px 0px 10px;
    }
  
    &__dadosPessoais{
      display: block;
      width: calc(100% - 220px);
      min-width: 200px;
      margin-left: 10px;
    }
  
    &__text{
      margin: 5px 0px 5px 0px;
      color: #30333C;
    }
  
    &__text-margin{
      margin: 5px 10px 5px 10px;
      color: #30333C;
    }
  
  }